@font-face {
    font-family: 'days';
    src: url("../fonts/Days-webfont.eot");
    src: url("../fonts/Days-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/Days-webfont.woff") format("woff"), url("../fonts/Days-webfont.ttf") format("truetype"), url("../fonts/Days-webfont.svg#pf_din_text_comp_promedium") format("svg");
    font-weight: normal;
    font-style: normal;
}

* {
    box-sizing: border-box;
}

html,
body,
div,
span,
h3,
p,
a,
img,
b,
i,
ul,
li,
form,
label,
table,
tbody,
tr,
td,
aside,
footer,
header,
nav,
section,
input,
button {
    margin: 0;
    padding: 0;
    border: 0;
    font: 16px eld, Georgia, sans-serif;
    vertical-align: baseline;
    outline: none;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

html {
    height: 100%;
}

body {
    background-repeat: no-repeat;
    background-color: #000000;
    background-size: 100%;
    position: relative;
    height: 100%;
}

#container {
    position: relative;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

header {
    background-image: url(../img/pokemon-logo.png);
    width: 100%;
    height: 15%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
    &:before {
        display: block;
        content: '';
        position: absolute;
        top: 0;
        width: 100%;
    }
}

.reg-ball-left,
.reg-ball-right {
    cursor: pointer;
    width: 34%;
    max-width: 295px;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
}

.reg-ball-left {
    left: -147px;
}

.reg-ball-right {
    right: -147px;
}

.popup-reg-ball-right,
.popup-reg-ball-left {
    background-image: url("../img/register-bg-min.png");
    background-position: -100% 0;
    width: 100%;
    max-width: 147px;
    height: 295px;
    position: absolute;
    z-index: 4;
    top: 50%;
    transform: translateY(-50%);
}

.reg-ball-left img,
.reg-ball-right img {
    width: 100%;
}

.popup-reg-ball-right img,
.popup-reg-ball-left img {
    width: 60%;
    position: relative;
    top: 40px;
    left: 12px;
    cursor: pointer;
}

.popup-reg-ball-right {
    right: 0;
    background-position: 0 0;
}

.popup-reg-ball-right img {
    left: 45px;
}

.social-1,
.social-2 {
    position: relative;
    width: 40px;
    top: 75px;
}

.social-1 {
    left: 70px;
}

.social-2 {
    right: -39px;
}

.icon {
    background-image: url("../img/social-icons-min.png");
    background-repeat: no-repeat;
    background-color: transparent;
    width: 39px;
    height: 39px;
    cursor: pointer;
}

.icon-footer-vk {
    background-position: -182px 0;
}

.icon-footer-facebook {
    background-position: -115px -64px;
}

.icon-footer-ok {
    background-position: -155px -39px;
}

.icon-footer-twitter {
    background-position: 0 0;
}

.icon-footer-instagram {
    background-position: -67px -64px;
}

.icon-footer-googleplus {
    background-position: -26px -39px;
}

.icon-1,
.icon-2,
.icon-3,
.icon-4,
.icon-5,
.icon-6 {
    position: relative;
}

.icon-2 {
    left: -30px;
    top: -8px;
}

.icon-3 {
    top: -26px;
    left: -68px;
}

.icon-5 {
    right: -30px;
    top: -10px;
}

.icon-6 {
    top: -29px;
    right: -68px;
}

.game {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    &:before,
    &:after {
        display: block;
        content: '';
        position: absolute;
        width: 100%;
        box-shadow: 0 0 40px 30px #004A94;
    }
    &:before {
        top: 0;
    }
    &:after {
        bottom: 0;
    }
}

iframe {
    border: 0;
    background-color: transparent;
    width: 100%;
    height: 100%;
}

#popup,
#popup-warning {
    width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1111;
    color: #ff9f15;
    display: none;
}

#popup-warning {
    background-color: black;
    color: #ffffff;
}

#popup-warning p {
    font-size: 26px;
    padding: 5px;
    width: 90%;
    margin: 0 auto;
}

.popup-container,
.popup-container-2 {
    position: relative;
    width: 90%;
    height: 335px;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
}

.popup-container-2 {
    background: url("../img/popup-bg-min.png") no-repeat center;
    background-size: 90% 100%;
    width: 95%;
}

.popup-reg-ball-left {
    left: 0;
    opacity: 1;
}

.popup-reg-ball-left img {
    left: -14px;
}

.popup-reg-ball-left .social-1 {
    left: 74px;
}

.popup-reg-ball-right {
    right: 0;
    opacity: 1;
}

.popup-reg-ball-right img {
    left: 18px;
}

.popup-reg-ball-right .social-2 {
    right: -34px;
}

.animate-shadow-left:after {
    display: table;
    content: '';
    position: relative;
    top: 32%;
    right: 147%;
    z-index: -1;
    background-image: url("../img/ball-lighter.png");
    width: 431px;
    height: 86px;
    animation: shadow 4s infinite ease-in-out;
    transform: rotateZ(90deg) translateX(-50%);
}

.animate-shadow-right:after {
    display: table;
    content: '';
    position: relative;
    top: 32%;
    left: -48%;
    z-index: -1;
    background: url(../img/ball-lighter.png) no-repeat;
    width: 431px;
    height: 86px;
    animation: shadow 4s infinite ease-in-out;
    transform: rotateZ(90deg) translateX(-50%);
}

.popup-title {
    position: relative;
    background-image: url("../img/register-bg-min.png");
    width: 147.5px;
    height: 295px;
    margin: 0 auto;
    top: 90px;
}

.popup-title img {
    position: relative;
    top: 40px;
    opacity: 0;
}

.popup-title:after {
    display: table;
    content: '';
    position: relative;
    top: 35px;
    z-index: -1;
    background-image: url("../img/ball-lighter.png");
    width: 431px;
    height: 86px;
    animation: shadow 4s infinite ease-in-out;
}

.popup-title-2 {
    position: relative;
    top: 47%;
    transform: translateY(-50%);
}

.popup-regform {
    position: absolute;
    left: 50%;
    z-index: 5555;
    transform: translateX(-50%);
    top: 100px;
    text-align: center;
    opacity: 0;
}

.popup-regform input {
    display: block;
    position: relative;
    top: 7px;
    width: 231px;
    height: 46px;
    margin: 0 auto;
    background-color: #000000;
    border: 2px solid #0064aa;
    color: #ffffff;
    padding: 0 0 0 45px;
    font-size: 16px;
    outline: none;
}

.popup-regform input::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    font-size: 16px;
    font-family: Verdana, sans-serif;
}

.popup-regform input::-moz-placeholder {
    /* Firefox 19+ */
    font-size: 16px;
    font-family: Verdana, sans-serif;
}

.popup-regform input:-ms-input-placeholder {
    /* IE 10+ */
    font-size: 16px;
    font-family: Verdana, sans-serif;
}

.popup-regform input:-moz-placeholder {
    /* Firefox 18- */
    font-size: 16px;
    font-family: Verdana, sans-serif;
}

.popup-regform button {
    position: relative;
    top: 40px;
    width: 231px;
    height: 46px;
    font-size: 14px;
    background: url(../img/buttons-sprite.png) repeat-x;
    background-position: 0 -331px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.75), inset 0 0 6px rgba(0, 0, 0, 0.75);
    border-radius: 4px;
    text-transform: uppercase;
    -webkit-transition: box-shadow .3s;
    transition: box-shadow .3s;
    box-sizing: border-box;
    font-weight: 700;
    color: #fff;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.35);
    text-align: center;
    cursor: pointer;
    font-family: Verdana, Tahoma, sans-serif;
}

.popup-regform button:hover {
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.8), inset 0 0 6px rgba(0, 0, 0, 0.75);
}

.popup-regform .regform-mail-icon {
    position: absolute;
    top: 20px;
    left: 14px;
}

.email-error,
.email-error-2 {
    width: inherit;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 60px;
    bottom: 10px;
    color: #ff0b00;
}

.email-error p,
.email-error-2 p {
    font-size: 14px;
}

.email-error-2 {
    top: 75px;
    left: 50%;
    transform: translateX(-50%);
}

.social-3 {
    position: relative;
    background-image: url("../img/register-bg-bottom-min.png");
    width: 431px;
    height: 191px;
    margin: 0 auto;
}

.social-3:after {
    display: table;
    content: '';
    position: relative;
    top: -37px;
    z-index: -1;
    background-image: url("../img/ball-lighter.png");
    width: 431px;
    height: 86px;
    animation: shadow 4s infinite ease-in-out;
}

.socials-reg-list {
    position: relative;
    width: 278px;
    top: 55px;
    margin: 0 auto;
    opacity: 0;
}

.socials-reg-list .socials-reg {
    display: inline-block;
    top: 0;
    left: 0;
    right: 0;
    padding: 0 2px;
}

.socials-reg-list .icon-4 {
    left: 25px;
}

.socials-reg-list .icon-3 {
    left: -25px;
}

.socials-reg-list .icon-5,
.socials-reg-list .icon-2 {
    top: 42px;
}

.socials-reg-list .icon-6,
.socials-reg-list .icon-1 {
    top: 65px;
}

@keyframes shadow {
    from {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.js-opacity-show {
    opacity: 1;
    z-index: 2;
    -webkit-transition: 1s;
    transition: 1s;
}

.js-display-hide {
    display: none;
}

.js-display-show {
    display: block;
    opacity: 1;
    z-index: 2;
    -webkit-transition: 1s;
    transition: 1s;
}

.js-error-visible {
    visibility: visible;
}

